import React, { Component } from 'react'
import {
  CButton,
  CCol,
  CContainer,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupAppend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import CIcon from '@coreui/icons-react'
import Axios from '../../../lib/api';
import swal from 'sweetalert';
class AcceptLink extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
    }
    this.otp = props.match.params.otp;
  }
  componentDidMount = () => {
    let user = JSON.parse(localStorage.getItem('user'));

    Axios.post('users/validatePin', { pin: this.otp }).then(r => {
      console.log(r.data)
      // localStorage.setItem("sample_token", r.data['auth-token']);

      this.setState({ loading: false })

      swal({
        title: "Successfully created.",
        text: "",
        icon: "success",
        buttons: {
          confirm: {
            text: "OK",
            value: true,
            visible: true,
            className: "",
            closeModal: true
          },
        }

      }).then(r => {
        this.props.history.push("/login")
      })
    })
      .catch(e => {
        console.log(e)
        this.setState({ loading: false })
      })
  }
  render() {

    return (
      <div className="c-app c-default-layout flex-row align-items-center">
        <CContainer>
          <CRow className="justify-content-center">
            <CCol md="6">
              <div className="clearfix">
                <h1 className="float-left display-3 mr-4">Wait</h1>
                <h4 className="pt-3">Validate Your email. <span className="spinner-border spinner-border-sm ml-1" role="status" aria-hidden="true"></span></h4>
                <p className="text-muted float-left">This will take 20 seconds.</p>

              </div>
            </CCol>
          </CRow>
        </CContainer>
      </div>
    )
  }
}

export default AcceptLink
